import { LinksFunction } from "@remix-run/node";
import styles from "~/styles/store.css";
export const links: LinksFunction = () => [
  {
    rel: "stylesheet",
    href: styles,
  },
];

const Store = () => {
  const items = [
    "First",
    "Second",
    "First",
    "Second",
    "First",
    "Second",
    "First",
    "Second",
    "First",
    "Second",
    "First",
    "Second",
    "First",
    "Second",
  ];
  return (
    <div className="container">
      <div className="items">
        {items.map((item, key) => (
          <div className="item" key={key}>
            {item}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Store;
